"use client";

import { useState } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import { motion } from "framer-motion";
import { Slider } from "@/components/ui/slider";
import { Switch } from "@/components/ui/switch";
import { Select, SelectTrigger, SelectValue, SelectContent, SelectItem } from "@/components/ui/select";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { ModeToggle } from "@/components/ui/mode-toggle";
import { ChartTooltip } from "@/components/ui/chart-tooltip";

type TimelinePoint = { time: string; soc: number };
type PredictResponse = {
  hours: number;
  final_battery: number;
  timeline: TimelinePoint[];
  normal_timeline: TimelinePoint[];
  start_time: string;
  end_time: string;
  cost_now: number;
  cost_optimized: number;
  savings: number;
  error?: string;
};

export default function EVOptimizerPage() {
  const [battery, setBattery] = useState<number>(25);
  const [power, setPower] = useState<number>(7);
  const [departure, setDeparture] = useState<string>("19:00");
  const [cheapMode, setCheapMode] = useState<boolean>(true);
  const [result, setResult] = useState<PredictResponse | null>(null);
  const [loading, setLoading] = useState<boolean>(false);

  const calculateCountdown = (time: string) => {
    const now = new Date();
    const [h,m] = time.split(":").map(Number);
    const target = new Date();
    target.setHours(h,m,0,0);
    if (target <= now) target.setDate(target.getDate()+1);
    const diff = target.getTime() - now.getTime();
    const hrs = Math.floor(diff / (3600*1000));
    const mins = Math.floor((diff % (3600*1000))/60000);
    return `${hrs}h ${mins}m`;
  };

  const handleOptimize = async () => {
    setLoading(true);
    setResult(null);

    try {
      const res = await fetch("http://localhost:8000/predict", {
        method: "POST",
        headers: {"Content-Type": "application/json"},
        body: JSON.stringify({ battery_level: battery, charger_power: power, target_time: departure, cheap_mode: cheapMode }),
      });
      const data: PredictResponse = await res.json();
      setResult(data);
    } catch (err) {
      alert("‚ö†Ô∏è Error connecting to backend!");
    } finally { setLoading(false); }
  };

  const intervalHours = 0.25; // 15min
  const normalHours = result ? result.normal_timeline.length * intervalHours : 0;

  return (
    <main className="min-h-screen bg-background text-foreground p-4 md:p-6">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-3xl md:text-4xl font-bold">‚ö° Smart EV Charging Optimization</h1>
        <ModeToggle />
      </div>

      <div className="max-w-5xl mx-auto grid grid-cols-1 md:grid-cols-2 gap-6">
        <Card>
          <CardHeader><CardTitle>Charging Settings</CardTitle></CardHeader>
          <CardContent className="space-y-4">
            <div>
              <label>Battery Level: {battery}%</label>
              <Slider value={[battery]} min={0} max={100} step={1} onValueChange={v=>setBattery(v[0])}/>
            </div>

            <div>
              <label>Charger Power</label>
              <Select value={power.toString()} onValueChange={v=>setPower(Number(v))}>
                <SelectTrigger><SelectValue/></SelectTrigger>
                <SelectContent>
                  <SelectItem value="3">3 kW</SelectItem>
                  <SelectItem value="7">7 kW</SelectItem>
                  <SelectItem value="11">11 kW</SelectItem>
                  <SelectItem value="22">22 kW</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div>
              <label>Departure Time</label>
              <input type="time" value={departure} onChange={e=>setDeparture(e.target.value)} className="w-full p-2 rounded-md bg-secondary"/>
            </div>

            <div className="flex justify-between items-center">
              <span>Optimize by electricity cost</span>
              <Switch checked={cheapMode} onCheckedChange={setCheapMode}/>
            </div>

            <Button onClick={handleOptimize} disabled={loading} className="w-full">
              {loading ? "‚è≥ Optimizing..." : "üî• Optimize Charging"}
            </Button>
          </CardContent>
        </Card>

        <Card>
          <CardHeader><CardTitle>Recommendation</CardTitle></CardHeader>
          <CardContent>
            {!result && <p>No result yet ‚Äî adjust values and press Optimize.</p>}

            {result && (
              <motion.div initial={{opacity:0, y:10}} animate={{opacity:1, y:0}} transition={{duration:0.3}}>
                <div className="space-y-2 mb-4">
                  <div className="flex justify-between">
                    <span>‚è± Countdown</span>
                    <span>{calculateCountdown(departure)}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>üöó Charging Needed</span>
                    {cheapMode ? (
                      <>
                        <span>Optimized: {result.hours.toFixed(2)}h</span>
                        <span className="ml-2 text-blue-500">Normal: {normalHours.toFixed(2)}h</span>
                      </>
                    ) : (
                      <span>{normalHours.toFixed(2)}h</span>
                    )}
                  </div>
                  <div className="flex justify-between">
                    <span>üîã Final Battery</span>
                    <span>{result.final_battery}%</span>
                  </div>
                  <div className="flex justify-between">
                    <span>üí∞ Cost (now)</span>
                    <span>RM {result.cost_now.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>üí∏ Cost (optimized)</span>
                    <span>RM {result.cost_optimized.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>üéØ Savings</span>
                    <span className="text-green-500 font-semibold">RM {result.savings.toFixed(2)}</span>
                  </div>
                </div>

                <div className="h-56">
                  <ResponsiveContainer width="100%" height="100%">
                    <LineChart>
                      <XAxis dataKey="time"/>
                      <YAxis domain={[0,100]}/>
                      <Tooltip content={<ChartTooltip/>}/>
                      <Line data={result.normal_timeline} dataKey="soc" stroke="#8884d8" strokeWidth={2} name="Normal SOC"/>
                      {cheapMode && <Line data={result.timeline} dataKey="soc" stroke="#82ca9d" strokeWidth={2} name="Optimized SOC"/>}
                    </LineChart>
                  </ResponsiveContainer>
                </div>

                <p className="text-xs mt-2">
                  Start: {result.start_time} ‚Äî End: {result.end_time}
                </p>
              </motion.div>
            )}
          </CardContent>
        </Card>
      </div>
    </main>
  );
}
